import { useState } from 'react';
import 'react-multi-carousel/lib/styles.css';
import colorSharp2 from "../assets/img/color-sharp2.png"
import { SkillsCard } from "./SkillsCard";
import {SkillsInfoCard} from "./SkillsInfoCard";
import { SKILLS } from "../utils/data";
import { Tab, Nav } from "react-bootstrap";

export const Skills = () => {
  const [selectedSkill, setSelectedSkill] = useState(SKILLS[0].title);
  
  function onSelectedSkill(name)
  {
    setSelectedSkill(name);
  }
  return (
    <section className="skill" id="skills">
      <h2>Skills</h2>
        <Tab.Container id="projects-tabs" defaultActiveKey="Frontend">
          <Nav variant="pills" className="nav-pills mb-2 justify-content-center align-items-center" id="pills-tab">
              {
            SKILLS.map((project, index) => {
                return (
                <SkillsCard
                    key={index}
                    onClick={()=> onSelectedSkill(project.title)}
                    {...project}
                    />
                )
            })
            }
        </Nav>
        <Tab.Content id="slideInUp">
            <SkillsInfoCard skill={selectedSkill}/>                                
        </Tab.Content>
        </Tab.Container>  
        <img className="background-image-right" src={colorSharp2} alt='background-image-right'></img>
    </section>    
  )
}