export const ExperienceCard = ({detail})=>
{
    return(
        <div className="experience-card">
            <h6>{detail.title}</h6>
            <div className="work-duration">{detail.date}</div>
            <ul>
                {detail.responsbilities.map((item, index)=>{
                    return(<li key={index}>{item}</li>)
                })}
            </ul>
        </div>
    )
}
