export const ContactCard = ({icon, text}) =>
{
    function handleSubmit(text)
    {
        console.log(text)
    }
    return(
        <div className="contact-details-card" onClick={handleSubmit(text)}>
            <div className="icon">
                <img src={icon} alt="text"/>
            </div>
            <p>{text}</p>
        </div>
    )
}